import React, { Component, Fragment } from 'react';
import { CustomInput, Modal, ModalBody } from 'reactstrap';
import VersionList from './VersionsList';
import Icon from '@mdi/react';
import { mdiClose } from '@mdi/js';

export default class Versions extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };
    }

    onVersionChange = function (e) {
        if (e.target.value !== "older") {
            this.props.onRedirect(`/v/${e.target.value}`);
        }
        else {
            this.setState({ modal_history: true });
        }
    }.bind(this)

    close = function()
    {
        this.setState({ modal_history: false });        
    }.bind(this)

    render() {
        return (<Fragment>
            <Modal isOpen={this.state.modal_history} centered={true}  >
                <ModalBody>
                    <a className="close-btn" onClick={this.close}>
                        <Icon path={mdiClose} size={0.9} />
                    </a>
                    <VersionList selected={this.props.selected} />
                </ModalBody>
            </Modal>

            <CustomInput value={this.props.selected} type="select" className="versions" name="version" onChange={this.onVersionChange} >
                <option value="1.2.2">v. 1.2.2 (latest)</option>
                {/*
                    (!["1.2.2", "1.2.1"].includes(this.props.selected))
                        ? <option value={this.props.selected}>v. {this.props.selected}</option>
                        : null
                */}
                <option value="older">old versions</option>
            </CustomInput>
        </Fragment>
        );

    }
}