import React from 'react';
import { local_urls } from './urls';

const Home = React.lazy(() => import('./pages/Home'));
const Components = React.lazy(() => import('./pages/Components'));
const JOFGENGenerator = React.lazy(() => import('./pages/generator/Generator'));
const JOFGENDesigner = React.lazy(() => import('./pages/designer/Designer'));
const Icons = React.lazy(() => import('./pages/designer/Icons'));
const Translations = React.lazy(() => import('./pages/designer/Translations'));
const Translation_Generator = React.lazy(() => import('./pages/playground/TranslationsGenerator'));
const Playground = React.lazy(() => import('./pages/Playground'));
const Playground_Generator = React.lazy(() => import('./pages/playground/Playground_Generator'));
const Playground_Designer = React.lazy(() => import('./pages/playground/Playground_Designer'));
const ValidationMethods = React.lazy(() => import('./pages/cheatsheet/ValidationMethods'));
const CodeSheet = React.lazy(() => import('./pages/Codesheet'));
const WhatsNew = React.lazy(() => import('./pages/WhatsNew'));

export const routes = [
  { path: local_urls["version_prefix"], exact: true, component: Home },
  { path: `${local_urls["version_prefix"]}/components`, component: Components },
  { path: `${local_urls["version_prefix"]}/codesheet`, component: CodeSheet },  
  { path: `${local_urls["version_prefix"]}/playground`, component: Playground }
];

export const components_routes = [
  { path: `${local_urls["version_prefix"]}/components/generator`, component: JOFGENGenerator  },
  { path: `${local_urls["version_prefix"]}/components/designer`, exact:true, component: JOFGENDesigner },
  { path: `${local_urls["version_prefix"]}/components/designer/icons`, component: Icons },
  { path: `${local_urls["version_prefix"]}/components/designer/translations`, component: Translations },
  { path: `${local_urls["version_prefix"]}/components/designer/translation-generator`, component: Translation_Generator },
  { path: `${local_urls["version_prefix"]}/components/validation-methods`, component: ValidationMethods },
  { path: `${local_urls["version_prefix"]}/components/whats-new`, component: WhatsNew }
];

export const playground_routes = [ 
  { path: `${local_urls["version_prefix"]}/playground/generator`, component: Playground_Generator },
  { path: `${local_urls["version_prefix"]}/playground/designer`, component: Playground_Designer }
];

const CodeSheet_list = {
  main: React.lazy(() => import('./pages/cheatsheet/main')),
  typeBool: React.lazy(() => import('./pages/cheatsheet/typeBool')),
  typeColor: React.lazy(() => import('./pages/cheatsheet/typeColor')),
  typeFloat: React.lazy(() => import('./pages/cheatsheet/typeFloat')),
  typeInt: React.lazy(() => import('./pages/cheatsheet/typeInt')),
  typeP: React.lazy(() => import('./pages/cheatsheet/typeP')),
  typePack: React.lazy(() => import('./pages/cheatsheet/typePack')),
  typeRgx: React.lazy(() => import('./pages/cheatsheet/typeRgx')),
  typeSec: React.lazy(() => import('./pages/cheatsheet/typeSec')),
  typeStr: React.lazy(() => import('./pages/cheatsheet/typeStr')),
  typeFloat_il: React.lazy(() => import('./pages/cheatsheet/typeFloat_il')),
  typeInt_il: React.lazy(() => import('./pages/cheatsheet/typeInt_il')),
  typeRgx_il: React.lazy(() => import('./pages/cheatsheet/typeRgx_il')),
  typeStr_il: React.lazy(() => import('./pages/cheatsheet/typeStr_il'))
};

export const codesheet_routes = [
  { path: `${local_urls["version_prefix"]}/codesheet`, exact:true, name: "codesheet", component: CodeSheet_list.main },
  { path: `${local_urls["version_prefix"]}/codesheet/types/bool`, name: "bool", component: CodeSheet_list.typeBool },
  { path: `${local_urls["version_prefix"]}/codesheet/types/color`, name: "color", component: CodeSheet_list.typeColor },
  { path: `${local_urls["version_prefix"]}/codesheet/types/float`, name: "float", component: CodeSheet_list.typeFloat },
  { path: `${local_urls["version_prefix"]}/codesheet/types/float_il`, name: "float_il", component: CodeSheet_list.typeFloat_il },
  { path: `${local_urls["version_prefix"]}/codesheet/types/int`, name: "int", component: CodeSheet_list.typeInt },
  { path: `${local_urls["version_prefix"]}/codesheet/types/int_il`, name: "int_il", component: CodeSheet_list.typeInt_il },
  { path: `${local_urls["version_prefix"]}/codesheet/types/p`, name: "p", component: CodeSheet_list.typeP },
  { path: `${local_urls["version_prefix"]}/codesheet/types/pack`, name: "pack", component: CodeSheet_list.typePack },
  { path: `${local_urls["version_prefix"]}/codesheet/types/rgx`, name: "rgx", component: CodeSheet_list.typeRgx },
  { path: `${local_urls["version_prefix"]}/codesheet/types/rgx_il`, name: "rgx_il", component: CodeSheet_list.typeRgx_il },
  { path: `${local_urls["version_prefix"]}/codesheet/types/sec`, name: "sec", component: CodeSheet_list.typeSec },
  { path: `${local_urls["version_prefix"]}/codesheet/types/str`, name: "str", component: CodeSheet_list.typeStr },
  { path: `${local_urls["version_prefix"]}/codesheet/types/str_il`, name: "str_il", component: CodeSheet_list.typeStr_il }
];
